import "./Laurels.css";
import { SubHeading } from "../../components";
import { images, data } from "../../constants";

export default function Laurels() {
    return (
        <div className="restaurant__bg restaurant__wrapper section__padding" id="awards">
            <div className="restaurant__wrapper_info">
                <SubHeading title="Awards & recognition" />
                <h1 className="headtext__cormorant">Our Laurels</h1>

                <div className="restaurant__laurels_awards">
                    {data.awards.map((award) => (
                        <AwardCard award={award} key={award.title} />
                    ))}
                </div>
            </div>

            <div className="restaurant__wrapper_img">
                <img src={images.laurels} alt="laurels_img" />
            </div>
        </div>
    );
}

const AwardCard = ({ award: { imgUrl, title, subtitle } }) => (
    <div className="restaurant__laurels_awards-card">
        <img src={imgUrl} alt="awards" />
        <div className="restaurant__laurels_awards-card_content">
            <p className="p__cormorant" style={{ color: "#DCCA87" }}>
                {title}
            </p>
            <p className="p__opensans">{subtitle}</p>
        </div>
    </div>
);
